
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


export default {
components: {
Tabbar,
TabbarItem,
},
props: ['environment', 'video', 'user'],
data () {
return {
selected: {
environment: this.environment,
video: this.video,
user: this.user
}
}
}
}



